'use client';
import React, { useEffect, useMemo, useState } from 'react';
import apiClient from '../lib/api-client';

const SHIFT_DEFS = [
  { id: 1, label: 'Shift 1', time: '08:00 - 16:00' },
  { id: 2, label: 'Shift 2', time: '16:00 - 24:00' },
  { id: 3, label: 'Shift 3', time: '00:00 - 08:00' },
];

function Badge({ children, color = '#ddd' }) {
  return (
    <span style={{ display: 'inline-block', background: color, color: '#fff', padding: '6px 10px', borderRadius: 999, fontSize: 13 }}>
      {children}
    </span>
  );
}

export default function ShiftManager() {
  const [sites, setSites] = useState([]);
  const [site, setSite] = useState('');
  const [users, setUsers] = useState([]);
  const [groups, setGroups] = useState([]);
  const [assignments, setAssignments] = useState([]);

  const [groupName, setGroupName] = useState('');
  const [selectedUsers, setSelectedUsers] = useState(new Set());
  const [techQuery, setTechQuery] = useState('');

  const [selectedDate, setSelectedDate] = useState(new Date().toISOString().slice(0,10));
  const [selectedGroupToAssign, setSelectedGroupToAssign] = useState('');
  const [selectedShift, setSelectedShift] = useState(1);

  // load sites (deriving from users' vendor_cabang) and users for selected site
  useEffect(() => { loadSites(); }, []);

  // reload groups whenever selected site changes
  useEffect(() => {
    loadGroups();
  }, [site]);

  async function loadSites() {
    try {
      const res = await apiClient('/users?page=1&pageSize=1000');
      const rows = (res?.data ?? res) || [];
      // users may store site in different fields depending on source: prefer `site`, fall back to `vendor_cabang`
      const uniqueSites = Array.from(new Set(rows.map(r => r.site || r.vendor_cabang || ''))).filter(Boolean);
      setSites(uniqueSites);
      if (!site && uniqueSites[0]) setSite(uniqueSites[0]);
    } catch (err) {
      console.error('load sites', err);
    }
  }

  useEffect(() => { if (site) loadUsers(site); }, [site]);

  async function loadUsers(siteFilter) {
    try {
      const res = await apiClient(`/users?page=1&pageSize=1000&site=${encodeURIComponent(siteFilter)}`);
      const rows = (res?.data ?? res) || [];
      // only technicians are selectable
      setUsers(rows.filter(u => (u.role || '').toLowerCase() === 'technician'));
    } catch (err) { console.error('load users', err); }
  }

  async function loadGroups() {
    try {
      const url = site ? `/shift-groups?site=${encodeURIComponent(site)}` : '/shift-groups';
      const res = await apiClient(url);
      const rows = (res?.data ?? res) || [];
      setGroups(rows);
    } catch (err) { console.error('load groups', err); }
  }

  async function loadAssignments() {
    try {
      const url = `/shift-assignments?date=${selectedDate}` + (site ? `&site=${encodeURIComponent(site)}` : '');
      const res = await apiClient(url);
      const rows = (res?.data ?? res) || [];
      setAssignments(rows);
    } catch (err) { console.error('load assignments', err); setAssignments([]); }
  }

  useEffect(() => { loadAssignments(); }, [selectedDate, site]);

  function toggleUserSelection(id) {
    const s = new Set(selectedUsers);
    if (s.has(id)) s.delete(id); else s.add(id);
    setSelectedUsers(s);
  }

  async function createGroup() {
    if (!groupName || selectedUsers.size === 0) return alert('Please enter a group name and choose at least one technician');
    const payload = { name: groupName, members: Array.from(selectedUsers), site };
    try {
      await apiClient('/shift-groups', { method: 'POST', body: payload });
      setGroupName('');
      setSelectedUsers(new Set());
      await loadGroups();
      alert('Group created');
    } catch (err) { console.error('create group', err); alert('Failed to create group'); }
  }

  async function assignGroup() {
    if (!selectedGroupToAssign) return alert('Select a group to assign');
    const payload = { date: selectedDate, shift: selectedShift, groupId: selectedGroupToAssign, site };
    try {
      await apiClient('/shift-assignments', { method: 'POST', body: payload });
      await loadAssignments();
      alert('Assigned');
    } catch (err) { console.error('assign', err); alert('Failed to assign'); }
  }

  // Edit group UI state
  const [editGroupId, setEditGroupId] = useState('');
  const [editGroupName, setEditGroupName] = useState('');
  const [editGroupSelectedUsers, setEditGroupSelectedUsers] = useState(new Set());

  function startEditGroup(g) {
    setEditGroupId(g.id);
    setEditGroupName(g.name || '');
    setEditGroupSelectedUsers(new Set(g.members || []));
  }

  function toggleEditUserSelection(id) {
    const s = new Set(editGroupSelectedUsers);
    if (s.has(id)) s.delete(id); else s.add(id);
    setEditGroupSelectedUsers(s);
  }

  function cancelEditGroup() {
    setEditGroupId('');
    setEditGroupName('');
    setEditGroupSelectedUsers(new Set());
  }

  async function saveEditGroup() {
    if (!editGroupId) return;
    try {
      const payload = { name: editGroupName, members: Array.from(editGroupSelectedUsers), site };
      await apiClient(`/shift-groups/${editGroupId}`, { method: 'PUT', body: payload });
      await loadGroups();
      await loadAssignments();
      cancelEditGroup();
      alert('Group updated');
    } catch (err) { console.error('save edit group', err); alert('Failed to update group'); }
  }

  const groupsById = useMemo(() => {
    const m = {};
    groups.forEach(g => m[g.id] = g);
    return m;
  }, [groups]);

  const usersById = useMemo(() => {
    const m = {};
    users.forEach(u => { m[u.id] = u; });
    return m;
  }, [users]);

  const assignmentMap = useMemo(() => {
    const m = {};
    assignments.forEach(a => {
      const key = a.shift;
      if (!m[key]) m[key] = [];
      m[key].push(a);
    });
    return m;
  }, [assignments]);

  // assignments grouped by date for calendar view
  const assignmentsByDate = useMemo(() => {
    const m = {};
    assignments.forEach(a => {
      if (!a || !a.date) return;
      if (!m[a.date]) m[a.date] = [];
      m[a.date].push(a);
    });
    return m;
  }, [assignments]);

  // view mode: 'schedule' (list per shift) or 'calendar' (month view)
  const [viewMode, setViewMode] = useState('schedule');

  // calendar month state (based on selectedDate)
  const [calendarMonth, setCalendarMonth] = useState(() => {
    const d = new Date(selectedDate);
    return new Date(d.getFullYear(), d.getMonth(), 1);
  });

  useEffect(() => {
    const d = new Date(selectedDate);
    setCalendarMonth(new Date(d.getFullYear(), d.getMonth(), 1));
  }, [selectedDate]);

  function prevMonth() {
    setCalendarMonth(m => new Date(m.getFullYear(), m.getMonth() - 1, 1));
  }

  function nextMonth() {
    setCalendarMonth(m => new Date(m.getFullYear(), m.getMonth() + 1, 1));
  }

  function buildMonthDays(monthStart) {
    const startDay = new Date(monthStart);
    const firstDow = startDay.getDay(); // 0=Sun..6=Sat
    // normalize to Monday-first if desired; keep Sunday-first here
    const days = [];
    // Backfill previous month days
    for (let i = 0; i < firstDow; i++) {
      const dt = new Date(startDay.getFullYear(), startDay.getMonth(), i - firstDow + 1);
      days.push({ date: dt, inMonth: false });
    }
    // current month
    const month = startDay.getMonth();
    for (let d = 1; d <= 31; d++) {
      const dt = new Date(startDay.getFullYear(), month, d);
      if (dt.getMonth() !== month) break;
      days.push({ date: dt, inMonth: true });
    }
    // fill to complete weeks (multiple of 7)
    while (days.length % 7 !== 0) {
      const last = days[days.length - 1].date;
      const dt = new Date(last.getFullYear(), last.getMonth(), last.getDate() + 1);
      days.push({ date: dt, inMonth: false });
    }
    return days;
  }

  const monthDays = useMemo(() => buildMonthDays(calendarMonth), [calendarMonth]);

  return (
    <div style={{ display: 'flex', gap: 20 }}>
      <div style={{ width: 420 }}>
        <div style={{ marginBottom: 12 }}>
          <label style={{ display:'block', marginBottom:6 }}>Site</label>
          {sites.length === 0 ? (
            <div style={{ padding:12, border:'1px dashed #eee', borderRadius:6, color:'#666' }}>
              Tidak ada site terdaftar. Pastikan pengguna memiliki field `site` pada profil mereka.
            </div>
          ) : (
            <select value={site} onChange={e => setSite(e.target.value)} style={{ width:'100%', padding:8 }}>
              <option value="">-- pilih site --</option>
              {sites.map(s => <option key={s} value={s}>{s}</option>)}
            </select>
          )}
        </div>

        <div style={{ border: '1px solid #eee', padding: 12, borderRadius:8, marginBottom:12, background:'#fff' }}>
          <h3 style={{ marginTop:0 }}>Create Group</h3>
          <div style={{ marginBottom:8 }}>
            <input placeholder="Group name" value={groupName} onChange={e => setGroupName(e.target.value)} style={{ width:'100%', padding:8 }} />
          </div>

          <div style={{ maxHeight: 260, overflow:'auto', border:'1px solid #f2f2f2', padding:8 }}>
            {users.length === 0 && <div style={{ color:'#777' }}>No technicians for this site</div>}
            <div style={{ marginBottom:8 }}>
              <input placeholder="Cari teknisi (nama atau NIPP)" value={techQuery} onChange={e => setTechQuery(e.target.value)} style={{ width:'100%', padding:8 }} />
            </div>
            {users.filter(u => {
              if (!techQuery) return true;
              const q = techQuery.toLowerCase();
              return (u.name || '').toLowerCase().includes(q) || String(u.nipp || '').includes(q);
            }).map(u => (
              <label key={u.id} style={{ display:'flex', gap:8, alignItems:'center', padding:'6px 4px' }}>
                <input type="checkbox" checked={selectedUsers.has(u.id)} onChange={() => toggleUserSelection(u.id)} />
                <div>
                  <div style={{ fontWeight:700 }}>{u.name || u.nipp || u.email}</div>
                  <div style={{ fontSize:12, color:'#666' }}>{u.nipp} • {u.role}</div>
                </div>
              </label>
            ))}
          </div>

          <div style={{ marginTop:8, textAlign:'right' }}>
            <button onClick={createGroup} style={{ padding:'8px 12px' }}>Create</button>
          </div>
        </div>

        <div style={{ border: '1px solid #eee', padding:12, borderRadius:8, marginBottom:12, background:'#fff' }}>
          <h3 style={{ marginTop:0 }}>Existing Groups</h3>
          {groups.length === 0 ? (
            <div style={{ color:'#666' }}>No groups created yet.</div>
          ) : (
            <div style={{ display:'grid', gap:8 }}>
              {groups.map(g => (
                <div key={g.id} style={{ padding:8, border:'1px solid #f1f1f1', borderRadius:6 }}>
                  {editGroupId === g.id ? (
                    <div>
                      <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
                        <input value={editGroupName} onChange={e => setEditGroupName(e.target.value)} style={{ flex:1, padding:8, marginRight:8 }} />
                        <div style={{ color:'#666', fontSize:12 }}>{g.site || ''} • {g.members?.length || 0} members</div>
                      </div>
                      <div style={{ marginTop:8, maxHeight:180, overflow:'auto', border:'1px solid #f8f8f8', padding:8 }}>
                        {users.filter(u => (u.role || '').toLowerCase() === 'technician').map(u => (
                          <label key={u.id} style={{ display:'flex', gap:8, alignItems:'center', padding:'4px 0' }}>
                            <input type="checkbox" checked={editGroupSelectedUsers.has(u.id)} onChange={() => toggleEditUserSelection(u.id)} />
                            <div>
                              <div style={{ fontWeight:700 }}>{u.name || u.nipp || u.email}</div>
                              <div style={{ fontSize:12, color:'#666' }}>{u.nipp}</div>
                            </div>
                          </label>
                        ))}
                      </div>
                      <div style={{ marginTop:8, textAlign:'right' }}>
                        <button onClick={saveEditGroup} style={{ padding:'6px 10px', marginRight:8 }}>Save</button>
                        <button onClick={cancelEditGroup} style={{ padding:'6px 10px' }}>Cancel</button>
                      </div>
                    </div>
                  ) : (
                    <div>
                      <div style={{ display:'flex', alignItems:'center', justifyContent:'space-between' }}>
                        <div style={{ fontWeight:700 }}>{g.name}</div>
                        <div style={{ color:'#666', fontSize:12 }}>{g.site || ''} • {g.members?.length || 0} members</div>
                      </div>
                      <div style={{ marginTop:8 }}>
                        { (g.members || []).map(id => (
                          <div key={id} style={{ fontSize:13 }}>{usersById[id]?.name || usersById[id]?.nipp || id}</div>
                        ))}
                      </div>
                      <div style={{ marginTop:8, textAlign:'right' }}>
                        <button onClick={() => startEditGroup(g)} style={{ padding:'6px 10px', marginRight:8 }}>Edit</button>
                        <button onClick={async () => {
                          if (!confirm(`Hapus grup "${g.name}"? Ini juga akan menghapus penugasan shift yang terkait.`)) return;
                          try {
                            await apiClient(`/shift-groups/${g.id}`, { method: 'DELETE' });
                            await loadGroups();
                            await loadAssignments();
                            alert('Group deleted');
                          } catch (err) {
                            console.error('delete group', err); alert('Failed to delete group');
                          }
                        }} style={{ padding:'6px 10px' }}>Delete</button>
                      </div>
                    </div>
                  )}
                </div>
              ))}
            </div>
          )}
        </div>

        <div style={{ border: '1px solid #eee', padding:12, borderRadius:8, background:'#fff' }}>
          <h3 style={{ marginTop:0 }}>Assign Group to Shift</h3>
          <div style={{ display:'flex', gap:8, marginBottom:8 }}>
            <input type="date" value={selectedDate} onChange={e => setSelectedDate(e.target.value)} style={{ padding:8 }} />
            <select value={selectedShift} onChange={e => setSelectedShift(Number(e.target.value))} style={{ padding:8 }}>
              {SHIFT_DEFS.map(s => <option key={s.id} value={s.id}>{s.label} — {s.time}</option>)}
            </select>
          </div>

          <div style={{ marginBottom:8 }}>
            <select value={selectedGroupToAssign} onChange={e => setSelectedGroupToAssign(e.target.value)} style={{ width:'100%', padding:8 }}>
              <option value="">-- pilih grup --</option>
              {groups.map(g => <option key={g.id} value={g.id}>{g.name} ({g.members?.length || 0})</option>)}
            </select>
          </div>
          <div style={{ textAlign:'right' }}>
            <button onClick={assignGroup} style={{ padding:'8px 12px' }}>Assign</button>
          </div>
        </div>
      </div>

      <div style={{ flex:1 }}>
        <div style={{ marginBottom:12, display:'flex', gap:12, alignItems:'center' }}>
          <h3 style={{ margin:0 }}>{viewMode === 'calendar' ? 'Calendar' : `Schedule — ${selectedDate}`}</h3>
          <div style={{ marginLeft:12, display:'flex', gap:8 }}>
            <button onClick={() => setViewMode('schedule')} style={{ padding:'6px 10px', background: viewMode === 'schedule' ? '#e6f0ff' : undefined }}>Schedule</button>
            <button onClick={() => setViewMode('calendar')} style={{ padding:'6px 10px', background: viewMode === 'calendar' ? '#e6f0ff' : undefined }}>Calendar</button>
          </div>
          <div style={{ marginLeft:'auto', display:'flex', alignItems:'center', gap:8 }}>
            {viewMode === 'calendar' ? (
              <>
                <button onClick={prevMonth} style={{ padding:'6px 10px' }}>◀</button>
                <div style={{ minWidth:200, textAlign:'center', fontWeight:700 }}>{calendarMonth.toLocaleString(undefined, { month: 'long', year: 'numeric' })}</div>
                <button onClick={nextMonth} style={{ padding:'6px 10px' }}>▶</button>
              </>
            ) : (
              <input type="date" value={selectedDate} onChange={e => setSelectedDate(e.target.value)} />
            )}
          </div>
        </div>
        {viewMode === 'calendar' ? (
          <div style={{ display:'grid', gap:8 }}>
            <div style={{ display:'grid', gridTemplateColumns: 'repeat(7, 1fr)', gap:4, marginBottom:4, textAlign:'center', color:'#555' }}>
              {['Sun','Mon','Tue','Wed','Thu','Fri','Sat'].map(d => <div key={d} style={{ fontWeight:700 }}>{d}</div>)}
            </div>
            <div style={{ display:'grid', gridTemplateColumns: 'repeat(7, 1fr)', gap:8 }}>
              {monthDays.map(({ date, inMonth }) => {
                const dStr = date.toISOString().slice(0,10);
                const dayAssigns = assignmentsByDate[dStr] || [];
                return (
                  <div key={dStr} onClick={() => { setSelectedDate(dStr); setViewMode('schedule'); }} style={{ minHeight:120, padding:8, borderRadius:6, background: inMonth ? '#fff' : '#fafafa', border: '1px solid #eee', cursor: 'pointer' }}>
                    <div style={{ display:'flex', justifyContent:'space-between', alignItems:'center' }}>
                      <div style={{ fontWeight:700 }}>{date.getDate()}</div>
                      <div style={{ fontSize:11, color:'#999' }}>{dStr === (new Date().toISOString().slice(0,10)) ? 'Today' : ''}</div>
                    </div>
                    <div style={{ marginTop:8, display:'flex', flexDirection:'column', gap:6 }}>
                      {SHIFT_DEFS.map(s => {
                        const sAssigns = dayAssigns.filter(a => Number(a.shift) === s.id);
                        return sAssigns.length === 0 ? null : (
                          <div key={s.id} style={{ display:'flex', flexDirection:'column', gap:4 }}>
                            <div style={{ fontSize:12, color:'#444', fontWeight:600 }}>{s.label}</div>
                            {sAssigns.map(sa => {
                              const group = groupsById[sa.groupId];
                              return (<div key={sa.id}><Badge color="#2563eb">{group ? group.name : `Group ${sa.groupId}`}</Badge></div>);
                            })}
                          </div>
                        );
                      })}
                    </div>
                  </div>
                );
              })}
            </div>
          </div>
        ) : (
          <div style={{ display:'grid', gap:12 }}>
          {SHIFT_DEFS.map(s => {
            const assigns = assignmentMap[s.id] || [];
            return (
              <div key={s.id} style={{ border: '1px solid #eee', borderRadius:8, padding:12, background:'#fff', display:'flex', alignItems:'center', justifyContent:'space-between' }}>
                <div>
                  <div style={{ fontWeight:800 }}>{s.label} <span style={{ fontWeight:400, color:'#666', marginLeft:8 }}>{s.time}</span></div>
                  {assigns.length > 0 ? (
                    <div style={{ marginTop:6 }}>
                      {assigns.map(ass => {
                        const group = groupsById[ass.groupId];
                        return (
                          <div key={ass.id} style={{ marginTop:8 }}>
                            <Badge color="#2563eb">{group ? group.name : `Group ${ass.groupId}`}</Badge>
                            <div style={{ marginTop:6, color:'#444' }}>
                              {(group?.members || []).slice(0,5).map(id => {
                                const u = usersById[id] || { name: id };
                                return <div key={id} style={{ fontSize:13 }}>{u.name || u.nipp || id}</div>;
                              })}
                              {group?.members && group.members.length > 5 && <div style={{ fontSize:12, color:'#666' }}>+{group.members.length - 5} more</div>}
                            </div>
                          </div>
                        );
                      })}
                    </div>
                  ) : (
                    <div style={{ marginTop:6, color:'#777' }}>Not assigned</div>
                  )}
                </div>

                <div style={{ textAlign:'right' }}>
                  {assigns.map(ass => (
                    <div key={ass.id} style={{ marginBottom:12 }}>
                      <div style={{ fontSize:13, color:'#666' }}>{`assigned by ${ass.created_by || '—'}`}</div>
                      <div style={{ marginTop:8 }}>
                        <button onClick={async () => {
                          if (!confirm('Remove assignment?')) return;
                          try {
                            await apiClient(`/shift-assignments/${ass.id}`, { method: 'DELETE' });
                            await loadAssignments();
                            alert('Removed');
                          } catch (err) { console.error(err); alert('Failed'); }
                        }}>Remove</button>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            );
          })}
        </div>
        )}

      </div>
    </div>
  );
}
